import React from "react"
import ReactDOM from "react-dom"
import "antd/dist/antd.css"
import { Provider } from "react-redux"

import store from "./redux/store"
import "./index.css"
import App from "./App"

ReactDOM.render(
  <Provider store={store}>
    <React.StrictMode>
      <App />
    </React.StrictMode>
  </Provider>,
  document.getElementById("root")
)
